.aboutus {
  width: 80vw;
  width: 50%;
  margin: 0 auto;
}

.aboutus h1 {
  margin-top: 8rem;
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
}

.aboutus h2 {
  margin-top: 1rem;
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 23px;
}

.aboutus p {
  text-align: left;
  color: white;
  font-weight: 400;
  font-size: 25px;
  margin: 1.5rem;
}

.aboutus__cards{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}