.card{
    background-color: #1E1E1E;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 8rem;
    width: 90vh;
    margin: 3vh 3vw;
}

.card__image{
    max-width: 5.5rem;
    max-height: 5.5rem;
    border-radius: 50%;
    border: 2px solid white;
}

.card__image-text-wrapper{
    max-height: 14rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
}